


























import Vue from 'vue';
import {DocItem} from '@/configs';

export default Vue.extend({
  name: 'Help',
  data() {
    return {
      filter: '',
    };
  },
  computed: {
    show(): Array<DocItem> {
      // ts bug
      return (this.$store.state.docConfig as Array<DocItem>).filter(
        value => value.name.toLowerCase().includes(this.filter.toLowerCase()) 
          && value.route);
    },
    showNavi(): boolean {
      return !this.isMobile || this.$route.path == '/help/index';
    },
    isMobile(): boolean {
      return this.$store.state.isMobile;
    },
  },
  watch: {
    show() {
      if (this.show.length > 0 && !this.isMobile) {
        const route = '/help/' + this.show[0].route;
        route != this.$route.path && this.$router.replace(route);
      }
    },
  },
});
